@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.b7336e49.ttf") format("truetype");
  font-weight: 500;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Roboto, -apple-system, BlinkMacSystemFont, Segoe UI, Arial, sans-serif;
}

header {
  background-color: #fff;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 16px;
  display: flex;
  position: relative;
}

.logo {
  margin-top: 5px;
  margin-left: 34px;
}

nav {
  margin-left: auto;
  margin-right: 34px;
}

nav ul {
  height: 100%;
  list-style: none;
  display: flex;
}

nav ul li {
  justify-content: center;
  align-items: center;
  height: 100%;
  display: flex;
}

nav ul li:not(:first-child) {
  margin-left: 20px;
}

.nav-link {
  color: #000;
  text-align: center;
  text-transform: uppercase;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 12px;
  font-weight: 500;
  line-height: 60px;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav-link:hover, .nav-link.is-active {
  color: #00acdc;
}

.nav-link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

[data-qa="hover"] {
  color: #000;
}
